import request from '@/utils/request'

// 获取地址列表
export const getAddressList = () => {
  return request.get('/address/list')
}

// 添加收货地址
export const setAddressList = (name, phone, region, detail) => {
  return request.post('/address/add', {
    form: {
      name,
      phone,
      region,
      detail
    }
  })
}

// 设置默认收货地址
export const setAddressId = (addressId) => {
  return request.post('/address/setDefault', {
    addressId
  })
}

// 删除收货地址
export const delAddressId = (addressId) => {
  return request.post('/address/remove', {
    addressId
  })
}

// 编辑收货地址
export const editAddressList = (addressId, name, phone, region, detail) => {
  return request.post('/address/add', {
    addressId,
    form: {
      name,
      phone,
      region,
      detail
    }
  })
}
